body{
  background-color: #9266ff;
}

.App{
  background-color: aqua;
  min-height: 500px;
  width: 400px;
  margin: 40px auto;
}

#to-do-form input{
  background-color: bisque;
  border: 0;
  width: 200px;
  height: 50px;
  padding: 0 20px;
  margin: 20px;
  font-size: 18px;
  border-radius: 10px;
}

#to-do-form input::placeholder{
  color: black;
}

#to-do-form input:focus{
  outline: none;
}

#to-do-form button{
  height: 50px;
  width: 80px;
  background-color: orangered;
  border-radius: 5px;
  border: 0;
  cursor: pointer;
  font-weight: bolder;
  color: white;
}